.main {
  padding: 40px 0;
}

.list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -15px;
}

.item {
  max-width: 294px;
  margin: 15px;
}

.title {
  margin-bottom: 2px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.16667;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}

.poster {
  height: 441px;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  object-fit: cover;
  transition: all 300ms;

  /* height: 441px;
  position: relative;
  margin-bottom: 10px;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer; */
}

.poster:hover {
  transform: scale(1.01);
  box-shadow: 0px 0px 20px 3px #ff6b08;
}

.rating {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;

  width: 36px;
  height: 16px;
  background-color: #ff6b08;
  font-weight: 500;
  border-radius: 5px;
  text-decoration: none;
  line-height: 1.16667;
  color: #ffffff;
}
